/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>Saturday in Vienna... a beautiful sunny morning, and we're sitting in a dark hall with our notebooks. The title of <a title=\"what's in rebecca's pocket?\" href=\"http://www.rebeccablood.net/\">Rebecca Blood's</a> keynote, <a href=\"http://193.171.60.17:8080/blogtalk/Filer/filetree/presentations/wagingpeace.pdf\">Waging Peace: Using Our Powers for Good</a>, sounded boring but her point was actually pretty interesting: that we tend to fill our blogrolls with blogs that think and write like we do. She then gave two examples of blogs that go beyond that, that attempt to bring together two sides of long-running conflicts for constructive dialog: <a title=\"Slugger O'Toole\" href=\"http://www.sluggerotoole.com/\">Slugger O'Toole</a> (Northern Ireland) and <a title=\"DialogNow || Open forum for discussing India-Pakistan relations\" href=\"http://www.dialognow.org/\">DialogNow</a> (India/Pakistan). </p>\n<p>I've always tried to put a wide range of opinions in my blogroll (which is not just for show, it's what I actually read), but I've never taken it beyond that. What are the prerequsites for establishing such a dialog? A passion for the issue and resolving the conflict, of course, what what else? Cross-cultural experience? Being bilingual (when language plays a role)? Or is it just being able to see beyond the box?</p>","fields":{"title":"Waging Peace","date":"24 May 2003","url":"https://papascott.de/2003-05-24-waging-peace/"}}},"pathContext":{"slug":"/2003-05-24-waging-peace/","prev":{"fields":{"title":"Drugstore of Hope","date":"24 May 2003","slug":"/2003-05-24-drugstore-of-hope/"}},"next":{"fields":{"title":"Don't blog me, I'm real!","date":"25 May 2003","slug":"/2003-05-25-dont-blog-me-im-real/"}}}}

/***/ })

